import AudioManager from "../../script/manager/AudioManager";
import LTSDK from "../../SDK/LTSDK";
import Awaiters from "../Async/Awaiters";
import LTUI from "../UIExt/LTUI";
import DefaultDevice from "./DefaultDevice";
import DefaultPlatform from "./DefaultPlatform";
import { EPlatformType } from "./EPlatformType";
import KSRecordManager from "./Impl/KS/KSRecordManager";
import LTPlatform from "./LTPlatform";
export default class KSPlatform extends DefaultPlatform {
    constructor() {
        super(...arguments);
        this.platform = EPlatformType.KS;
        this.safeArea = null;
        this.device = new DefaultDevice();
        this.systemInfo = null;
        /**
         * 
         * fake true,
         */
        this.isSupportJumpOther = false;
    }
    Init(platformData) {
        this.loginState = {
            isLogin: false,
            code: null
        };
        this.base = window["kwaigame"];
        if (this.base == null) {
            console.error("", LTPlatform.platformStr);
            return;
        }
        else {
            console.error("", LTPlatform.platformStr);
        }
        this.platformData = platformData;
        // this._InitLauchOption(); 
        this.readyGo();
        this.base.init({ "appId": platformData.appId });
        this.recordManager = new KSRecordManager(this.base);
        try {
            this.getSystemInfo();
        }
        catch (error) {
            console.error('', JSON.stringify(error));
        }
        // console.log(112);
        // console.log(113);
        // this.createVideo();
    }
    readyGo() {
        this.base.readyGo();
    }
    willClose() {
        this.base.willClose();
    }
    _InitLauchOption() {
        // onShow
        // this.base.onShow(this._OnShow);
        // this.base.onHide(this._OnHide);
        // // 
        // let res = this.base.getLaunchOptionsSync() as LTGame.LaunchOption;
        // this._OnShow(res);
    }
    _login() {
        this.base.login({
            success: (res) => {
                console.log(' ', res);
                if (res.gameToken) {
                    this.loginState = { code: res.gameToken, isLogin: true };
                    this.onLoginEnd.run();
                }
            },
            fail: (err) => {
                console.error(' ', err);
                this.loginState = { code: 'null', isLogin: false };
            }
        });
    }
    authorize() {
        this.base.authorize({
            scope: "Scope.userInfo",
            success: () => {
                console.log("");
            },
            fail: (error) => {
                console.log(": " + JSON.stringify(error));
            },
            complete: () => {
                console.log("");
            }
        });
    }
    getSetting() {
        this.base.getSetting({
            success: (result) => {
                console.log("" + JSON.stringify(result));
            },
            fail: (error) => {
                console.log(": " + JSON.stringify(error));
            },
            complete: () => {
                console.log("");
            }
        });
    }
    getUserInfo() {
        return new Promise((resolve, reject) => {
            this.base.getUserInfo({
                success: (result) => {
                    console.log("" + JSON.stringify(result));
                    resolve();
                },
                fail: (error) => {
                    console.log(": " + JSON.stringify(error));
                },
                complete: () => {
                    console.log("");
                    resolve();
                }
            });
        });
    }
    /**
     * 
     */
    _OnShow(res) {
        console.log(LTPlatform.platformStr, "OnShow", res);
        LTPlatform.instance.lauchOption = res;
        LTPlatform.instance._CheckUpdate();
        AudioManager.instance.Resume();
        Awaiters.NextFrame().then(() => {
            if (LTPlatform.instance.onResume) {
                LTPlatform.instance.onResume.runWith(res);
            }
            let cacheOnShow = LTPlatform.instance["_cacheOnShowHandle"];
            console.log(cacheOnShow);
            if (cacheOnShow) {
                cacheOnShow.run();
                LTPlatform.instance["_cacheOnShowHandle"] = null;
            }
        });
    }
    SetClipboardData(str) {
        console.log('KS SetClipboardData ');
    }
    /**
    * 
    */
    _OnHide(res) {
        console.log(LTPlatform.platformStr, "OnHide", res);
        AudioManager.instance.Pause();
        if (LTPlatform.instance.onPause) {
            LTPlatform.instance.onPause.runWith(res);
        }
    }
    getSystemInfo() {
        if (Laya.stage.height / Laya.stage.width > 1.95) {
            this.safeArea = { top: 80, bottom: Laya.stage.height, left: 0, right: Laya.stage.width, height: Laya.stage.height - 80, width: Laya.stage.width };
        }
        else {
            this.safeArea = { top: 0, bottom: Laya.stage.height, left: 0, right: Laya.stage.width, height: Laya.stage.height, width: Laya.stage.width };
        }
        window["kwaigame"].getSystemInfo({
            response: (res) => {
                this.systemInfo = res;
                console.log('systeminfo', JSON.stringify(res));
                console.log(this.safeArea);
            }
            // ,
            // fail: (err) => {
            //     console.error('systeminfo', JSON.stringify(err));
            // }
        });
    }
    IsBannerAvaliable() {
        return;
    }
    IsVideoAvaliable() {
        return this.base.isSupport(this.base.Support.features.RewardVideo);
    }
    IsInterstitalAvaliable() {
        return;
    }
    ShowInterstitalAd() {
        if (!this.platformData.interstitialId)
            return;
        if (!LTSDK.instance.configs.isADEnable)
            return;
        let interad = this.base.createInterstitialAd({
            adUnitId: this.platformData.interstitialId
        });
        if (interad) {
            interad.show().catch(err => console.log(err));
        }
    }
    ShowBannerAd() {
    }
    HideBannerAd() {
    }
    ShowRewardVideoAd(onSuccess, onSkipped) {
        this._rewardSuccessed = onSuccess;
        this._rewardSkipped = onSkipped;
        return new Promise((resolve, reject) => __awaiter(this, void 0, void 0, function* () {
            if (this._rewardVideo) {
                this._rewardVideo.show({
                    success: () => {
                    },
                    fail: (error) => {
                    }
                });
            }
            else {
                this._rewardVideo = this.base.createRewardedVideoAd({
                    adUnitId: this.platformData.rewardVideoId
                });
                this._rewardVideo.onClose((res) => {
                    console.log(": " + JSON.stringify(res));
                    if (this._rewardSkipped)
                        this._rewardSkipped.run();
                });
                this._rewardVideo.onReward((result) => {
                    console.log(": " + JSON.stringify(result));
                    if (this._rewardSuccessed)
                        this._rewardSuccessed.run();
                });
                if (this._rewardVideo) {
                    this._rewardVideo.show({
                        success: () => {
                        },
                        fail: (error) => {
                        }
                    });
                }
                else {
                    console.log("");
                }
            }
        }));
    }
    createVideo() {
        this._rewardVideo = this.base.createRewardedVideoAd({
            adUnitId: this.platformData.rewardVideoId
        });
        if (this._rewardVideo) {
            console.log("");
            this._rewardVideo.onClose((result) => {
                console.log(": " + JSON.stringify(result));
            });
            this._rewardVideo.onReward((result) => {
                console.log(": " + JSON.stringify(result));
            });
        }
        else {
            console.log("");
        }
    }
    GetFromAppId() {
        return null;
    }
    ShareAppMessage(obj, onSuccess = null, onFailed = null) {
        console.log("", obj);
        let params = {
            title: "",
            desc: "",
            iconUrl: "icon",
            imageUrl: "",
            extension: {
                name: "test"
            },
            response: (result) => {
                console.log(": " + JSON.stringify(result));
                if (onSuccess) {
                    onSuccess.run();
                }
            }
        };
        this.base.shareToMsg(params);
    }
    LoadSubpackage(name, onSuccess, onFailed, onProgress) {
        var subpackageSupport = this.base.isSupport(this.base.Support.features.Subpackage);
        console.log(`${name}`, subpackageSupport);
        if (!subpackageSupport) {
            //APPmain.js sence2sence2/main.js
            window.require(`${name}/main.js`);
            //    
            return;
        }
        let loadTask = this.base.loadSubpackage({
            name: name, success: function (res) {
                //	
                if (onSuccess) {
                    onSuccess.run();
                }
            }, fail: function (err) {
                console.log(err);
                onProgress.run();
            }
        });
        loadTask.onProgressUpdate(res => {
            console.log(res);
            if (onProgress) {
                onProgress.runWith(res.progress / 100);
            }
        });
    }
    RecordEvent(eventId, param) {
        console.log("", eventId, param);
    }
    ShareVideoInfo() {
        console.log(LTPlatform.platformStr, "");
    }
    _CheckUpdate() {
    }
    ShowToast(str) {
        LTUI.Toast(str);
    }
    OpenGameBox() {
        console.error("", LTPlatform.platformStr, "");
    }
    NavigateToApp(appid, path, extra) {
        return new Promise((resolve, reject) => {
            console.error("", LTPlatform.platformStr, `appid:${appid}`);
            // resolve
            resolve(false);
        });
    }
    createShortcut() {
        console.log('');
        return;
    }
    GetStorage(key) {
        console.log('');
        return Laya.LocalStorage.getItem(key);
    }
    SetStorage(key, data) {
        console.log('');
        Laya.LocalStorage.setItem(key, data);
    }
    /**
     *   CPServiceAccount : CP MiniGameOfficialAccount
     */
    followOfficialAccount() {
        return new Promise((resolve, reject) => {
            this.base.openUserProfile({
                accountType: "CPServiceAccount",
                callback: (res) => {
                    if (res && res.errorCode == 1) {
                        resolve(res.hasFollow);
                    }
                    else {
                        console.error(res.errorMsg);
                        reject(false);
                    }
                }
            });
        });
    }
    /**
     *   CPServiceAccount : CP MiniGameOfficialAccount
     */
    checkFollowState() {
        return new Promise((resolve, reject) => {
            this.base.checkFollowState({
                accountType: "CPServiceAccount",
                callback: (res) => {
                    if (res && res.errorCode == 1) {
                        resolve(!res.hasFollow);
                    }
                    else {
                        console.error(res.errorMsg);
                        resolve(false);
                    }
                }
            });
        });
    }
}
