import LTUI from "../../../UIExt/LTUI";
import DefaultPlatform from "../../DefaultPlatform";
import { EPlatformType } from "../../EPlatformType";
import LTPlatform from "../../LTPlatform";
export class NativeAndroid233Platform extends DefaultPlatform {
    constructor() {
        super(...arguments);
        this.useWAV = true;
        this.platform = EPlatformType.Native_Android_233;
        this.safeArea = null;
        /**233 180s  */
        this.fullScreenAdCD = 0;
        // 60s
        this.interAdCD = 0;
        this._isDebug = true;
    }
    ;
    Init(platformData) {
        this.platformData = platformData;
        let conchConfig = window['conchConfig'];
        let PlatformClass = window['PlatformClass'];
        let os = conchConfig.getOS();
        if (os == "Conch-android") {
            this.bridge = PlatformClass.createClass("demo.JSBridge"); //
        }
        console.log("" + LTPlatform.platformStr);
        this._InitSystemInfo();
        this.loginState = {
            isLogin: true,
            code: "111111111111"
        };
        this.userInfo = { nickName: '666', avatarUrl: '66.png' };
        Laya.timer.once(500, this, this.FakeLoginEnd);
        this.fullScreenAdCD = 180;
        this.interAdCD = 0;
        Laya.timer.loop(1000, this, () => {
            this.fullScreenAdCD -= 1;
            this.interAdCD -= 1;
        });
    }
    _InitSystemInfo() {
        try {
            this.safeArea = {};
            this.safeArea.left = 0;
            this.safeArea.right = 0;
            this.safeArea.bottom = 0;
            this.safeArea.top = 0;
            this.safeArea.width = 750;
            this.safeArea.height = 1334;
            this._cacheScreenScale = 750 / Laya.stage.width;
            console.log("");
        }
        catch (e) {
            console.error(e);
            console.error(",");
            this.safeArea = null;
        }
    }
    FakeLoginEnd() {
        if (this.onLoginEnd)
            this.onLoginEnd.run();
    }
    ShowRewardVideoAd(onSuccess, onSkipped) {
        const obj = {};
        LTUI.ShowLoading("...");
        this.bridge.callWithBack(function (json) {
            console.log(":" + json);
            const data = JSON.parse(json);
            //  {event:"", value:""}
            switch (data.event) {
                case "onAdClose":
                    //  
                    onSkipped.run();
                    break;
                case "onAdComplete":
                    //  
                    onSuccess.run();
                    break;
                case "onAdError":
                    //  
                    onSkipped.run();
                    break;
                case "onAdSkipped":
                    //  
                    onSkipped.run();
                    break;
            }
        }, "RewardVideoAd", JSON.stringify(obj));
        setTimeout(() => {
            LTUI.HideLoading();
        }, 5000);
        this.bridge.call("ShowRewardAd");
    }
    ShowInterstitalAd() {
        if (window["__GM"])
            return;
        if (this.fullScreenAdCD < 0) {
            this.ShowFullScreenVideo();
        }
        else if (this.interAdCD < 0) {
            this.interAdCD = 60;
            this.bridge.call("ShowInterstitialAd");
        }
    }
    ShowFullScreenVideo() {
        const obj = {};
        this.bridge.callWithBack((json) => {
            console.log(": " + json);
            const data = JSON.parse(json);
            //  {event:"", value:""}
            switch (data.event) {
                case "onAdClose":
                    //  
                    this.fullScreenAdCD = 180;
                    break;
                case "onAdComplete":
                    //  
                    this.fullScreenAdCD = 180;
                    break;
                case "onAdError":
                    //  
                    break;
                case "onAdSkipped":
                    //   
                    this.fullScreenAdCD = 180;
                    break;
            }
        }, "ShowFullScreenVideo", JSON.stringify(obj));
        this.bridge.call("ShowFullScreenVideo");
    }
    ShowBannerAd() {
        // if (LTSDK.instance.checkState != ECheckState.InCheck) {
        // }
        this.bridge.call("ShowBanner");
    }
    HideBannerAd() {
        this.bridge.call("RemoveBanner");
    }
    RecordEvent(eventId, param) {
        //splash
        if (eventId == "") {
            this.bridge.call('hideSplash');
        }
    }
}
