export default class DefaultDevice {
    Vibrate(isLong) {
        console.log("", isLong);
    }
}
export class OppoDevice {
    constructor() {
        this.base = qg;
        console.log('QGDIVCE INIT', this.base);
    }
    Vibrate(isLong) {
        console.log('', isLong);
        if (this.base) {
            if (isLong) {
                this.base.vibrateLong({
                    success: (res) => {
                        console.log('');
                    },
                    fail: (res) => { console.log(''); },
                    complete: (res) => { }
                });
            }
            else {
                this.base.vibrateShort({
                    success: (res) => { console.log(''); },
                    fail: (res) => { console.log(''); },
                    complete: (res) => { }
                });
            }
        }
    }
}
export class VivoDevice {
    constructor() {
        this.base = qg;
        console.log('QGDIVCE INIT', this.base);
    }
    Vibrate(isLong) {
        console.log('', isLong);
        if (this.base) {
            if (isLong) {
                this.base.vibrateLong();
            }
            else {
                this.base.vibrateShort();
            }
        }
    }
}
