import WXPlatform from "./WXPlatform";
import { EPlatformType } from "./EPlatformType";
import { CommonEventId } from "../Commom/CommonEventId";
export default class QTTPlatform extends WXPlatform {
    constructor() {
        super(...arguments);
        this.platform = EPlatformType.QTT;
    }
    Init(platformData) {
        this.base = window["qttGame"];
        if (this.base == null) {
            console.error("");
            return;
        }
        this.platformData = platformData;
        // this._InitLauchOption();
        // this._Login();
        // this._InitShareInfo();
        // this._InitSystemInfo();
        // this._CreateBannerAd();
        // this._CreateVideoAd();
        // this._CreateInterstitalAd();
        window["iplatform"] = this;
    }
    IsBannerAvaliable() {
        return true;
    }
    ShowBannerAd() {
        this.base.showBanner({ index: 1 });
    }
    HideBannerAd() {
        this.base.hideBanner();
    }
    IsVideoAvaliable() {
        return true;
    }
    ShowRewardVideoAd(onSuccess, onSkipped) {
        let options = {};
        options.index = 1; //1234...
        options.gametype = 1; //1()  2(laba)  3()
        options.rewardtype = 1; // 1
        options.data = {};
        options.data.title = ""; //
        Laya.stage.event(CommonEventId.PAUSE_AUDIO);
        this.base.showVideo((res) => {
            Laya.stage.event(CommonEventId.RESUM_AUDIO);
            if (res == 1) {
                // 
                if (onSuccess) {
                    onSuccess.run();
                }
            }
            else {
                //res = 0    
                //res = 2    
                if (onSkipped) {
                    onSkipped.run();
                }
            }
        }, options);
    }
    ShowInterstitalAd() {
        // 
        this.ShowHDReward();
    }
    /**
     * 
     */
    ShowHDReward() {
        let options = {};
        options.index = 1;
        options.rewardtype = 1;
        this.base.showHDReward(options);
    }
    RecordEvent(eventId, param) {
        console.log("", eventId, param);
    }
}
